import img1 from '../img/1.jpg';
import img4 from '../img/4.jpg';

export default function Main(){
    return(
        <>
        <div class="black"> 
            <figure>
                <img src={img1} alt='img1'/>
                <img src={img4} alt='img4'/>
            </figure>
            <div class="black_text">
                <p>The hidden interpretation of the photo while viewing it is in the viewer's mind</p>
                <p>Beyond the entrance, there is no exit, black aesthetics. We hope to keep you updated after this project is over.</p>
            </div>
        </div>
        </>
    );
}